import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs";
import { Page } from "./models/page.model";
import { environment } from "../environments/environment";
import { Chat } from "./models/chat.model";
@Injectable()
export class OrderServices {
    constructor(private httpClient: HttpClient) { }
    path = "http://192.168.0.232:3002/api/v1/providers/" + environment.id + "/orders";
    basePath = "http://192.168.0.232:3002/api/v1/";
    token = { headers: new HttpHeaders().set('Authorization', 'Bearer  eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOjMxNSwiaXNzIjoiQXBwIiwiaWF0IjoxNTM2NDkyMTA5NzgxLCJleHAiOjE1MzY0OTIxMTY5ODF9.wYPCAEDMcUC2JvmBXxT5z2w8Jf2KL7ilVgEsYLQPuOk') };
    fetchproviderorders(page: number): Observable<Page<any>> {
        let url = this.path + "?page=" + page + "&limit=" + 12;
        return this.httpClient.get<Page<any>>(url, this.token);
    }


    updateproviderorders(orderId: number, status: string) {
        let url = this.basePath + "orders/" + orderId + "/status";
        return this.httpClient.patch(url, { "status": status }, this.token);
    }
    updateprovideLocation(provider: number, body: any) {
        let url = this.basePath + 'providers/'+provider+'/current-location';
        return this.httpClient.put(url, body, this.token);
    }

    resetUnseenCount() {
        let url = this.basePath + 'users/'+environment.id+'/notifications/count-reset';
        return this.httpClient.put(url, {}, this.token);
    }

    getConversationId(person1: number, person2: number) {
        let url = this.basePath + "conversationId?id1=" + person1 + "&id2=" + person2;
        return this.httpClient.get(url, this.token);
    }

    getConversationMessages(conversationId): Observable<any[]> {
        let url = this.basePath + "chat/" + conversationId;
        return this.httpClient.get<any[]>(url, this.token);
    }

    sendMessage(messageObject, conversationId) {
        let url = this.basePath + "chat/" + conversationId;
        return this.httpClient.post(url, messageObject, this.token);
    }

    fetchproviderNotifications(page: number): Observable<Page<any>> {
        let url = this.basePath + "users/"+environment.id+"/notifications?page=" + page + "&limit=" + 12;
        return this.httpClient.get<Page<any>>(url, this.token);
    }

    getOrderById(orderId: number) {
        let url = this.basePath + "providers/"+environment.id+"/orders/"+orderId ;
        return this.httpClient.get(url, this.token);
    }
}