import { Component, OnInit, NgZone, ViewChild, ElementRef } from '@angular/core';
import { OrderServices } from '../order.service';
import { ActivatedRoute } from '@angular/router';
import { environment } from '../../environments/environment';
import { FormGroup, FormControl } from '@angular/forms';
import ds from '../da.service';
declare var $;
@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit {
  @ViewChild('conversationArea') chatBody: ElementRef;
  @ViewChild('img') myImg: ElementRef = null;
  person1;
  person2;
  conversationId;
  messages = [];
  time;
  listChat;
  chatForm: FormGroup;
  scrollToBottomFlage = false;
  constructor(private orderServices: OrderServices,
    private activeRoute: ActivatedRoute, private zone: NgZone) { }

  ngOnInit() {
    //inialize the form 
    this.chatForm = new FormGroup({
      'messageText': new FormControl(null, []),
    });

    //get the two persons 
    this.person1 = this.activeRoute.snapshot.params['clientId'];
    this.person2 = environment.id;

    //call get conversion id
    this.getConversationId(this.person1, this.person2);


    // link the chat html java script
    // this.chat();


    // this.listenToMessage();


  }




  getConversationId(person1, person2) {
    this.orderServices.getConversationId(person1, person2).subscribe(
      res => {
        this.conversationId = res['conversationId'];
        this.getConversationMessage(res['conversationId']);
        this.checkTyping();
        this.listenToMessage();
      },
      err => {
        console.log('error while getting getConversationId ==>', err)
      })
  }

  getConversationMessage(getConversationId) {
    this.orderServices.getConversationMessages(getConversationId).subscribe(
      (res) => {
        this.messages = res.reverse();
        this.scrollToBottomFlage = true;
      },
      err => {
        console.log('error while getting getConversationId ==>', err)
      })
  }

  sendMessage() {
    let img = this.myImg.nativeElement.files[0];
    let text = this.chatForm.get('messageText').value;
    if (!this.chatForm.get('messageText').value && !img) {
      return;
    }
    let message = new FormData();
    if (img)
      message.append('img', img);
    if (text)
      message.append('text', text);

    this.orderServices.sendMessage(message, this.conversationId)
      .subscribe(
        res => { 
          console.log('the result ===', res)
          this.chatForm.reset();
          this.myImg.nativeElement.value = "";
        },
        err => { console.log('the result ===', err) }
      )
  }

  listenToMessage() {
    console.log('this.conversationId ==>', this.conversationId);
    this.listChat = ds.record.getList(
      `dsChats/${this.conversationId}`
    );


    this.listChat.on('entry-added', (x) => {

      ds.record.getRecord(x).whenReady(message => {

        console.log(this.messages, 'the resulat of  fffff  ===', message.get().message,message.get().user);
        let newMessage = {
          createdAt:message.get().message.createdAt ,
          seen: message.get().message.createdAt,
          text: message.get().message.text,
          img: message.get().message.img,
         user:{
           name:message.get().message.user.name ,
           avatar: message.get().message.user.avatar,
              _id: message.get().message.user._id
         },
        _id: message.get().message._id
      };
        this.zone.run(() => {

          this.messages.push(newMessage);
          this.scrollToBottomFlage = true;

        });
    });

  })
}
emitIsTying() {
  console.log('the help', this.person1)
  ds.event.emit(
    `${this.conversationId}/${this.person1}`,
    { isTyping: true }
  );
}
checkTyping() {

  ds.event.subscribe(`${this.conversationId}/${this.person2}`,
    (type) => {
      if (type.isTyping && this.time) {
        this.time = setTimeout(() => {
          clearTimeout(this.time);
          // const index = this.messages.findIndex(
          //   i => i._id === "typingId"
          // );
          // console.log('the index of is typing methods 333333333 ====', index);
          // if (index > 0) {
          //   this.zone.run(() => {
          //     this.messages.splice(index, 1);
          //   });
          // }

        }, 10000);
      }

      if (type.isTyping) {
        console.log('the  typing event is 33333333 ===>', type);
        const fakeMsg = {
          _id: "typingId",
          text: ".........................",
          createdAt: "",
          user: {
            _id: this.person1,
            name: "",
            avatar: ""
          }
        };
        const index = this.messages.findIndex(
          i => i._id === "typingId"
        );
        if (index < 0) {
          this.zone.run(() => {
            this.messages.push(fakeMsg);
            this.scrollToBottomFlage = true;
          });
        }



        this.time = setTimeout(() => {
          clearTimeout(this.time);
          const index = this.messages.findIndex(
            i => i._id === "typingId"
          );
          console.log('the index of is typing methods ====', index);
          if (index > 0) {
            this.zone.run(() => {
              this.messages.splice(index, 1);
            });
          }

          // clearTimeout(this.time);
        }, 10000);






      }

    },

  )




}

ngAfterViewChecked() {
  if (this.scrollToBottomFlage) {

    this.scrollToBottom();
    this.scrollToBottomFlage = false;
  }

}
scrollToBottom(): void {
  try {
    this.chatBody.nativeElement.scrollTop = this.chatBody.nativeElement.scrollHeight;
  } catch(err) { }
}


chat() {
  (function () {
    var Message;
    Message = function (arg) {
      this.text = arg.text, this.message_side = arg.message_side;
      this.draw = function (_this) {
        return function () {
          var $message;
          $message = $($('.message_template').clone().html());
          $message.addClass(_this.message_side).find('.text').html(_this.text);
          $('.messages').append($message);
          return setTimeout(function () {
            return $message.addClass('appeared');
          }, 0);
        };
      }(this);
      return this;
    };
    $(function () {
      var getMessageText, message_side, sendMessage;
      message_side = 'right';
      getMessageText = function () {
        var $message_input;
        $message_input = $('.message_input');
        return $message_input.val();
      };
      sendMessage = function (text) {
        var $messages, message;
        if (text.trim() === '') {
          return;
        }
        $('.message_input').val('');
        $messages = $('.messages');
        message_side = message_side === 'left' ? 'right' : 'left';
        message = new Message({
          text: text,
          message_side: message_side
        });
        message.draw();
        return $messages.animate({ scrollTop: $messages.prop('scrollHeight') }, 300);
      };
      $('.send_message').click(function (e) {
        return sendMessage(getMessageText());
      });
      $('.message_input').keyup(function (e) {
        if (e.which === 13) {
          return sendMessage(getMessageText());
        }
      });
      // // sendMessage('Hello Philip! :)');
      // setTimeout(function () {
      //   return sendMessage('Hi Sandy! How are you?');
      // }, 1000);
      // return setTimeout(function () {
      //   return sendMessage('I\'m fine, thank you!');
      // }, 2000);
    });
  }.call(this));
}

}
