import { Component, OnInit, NgZone } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { OrderServices } from '../order.service';
import { environment } from '../../environments/environment';
import ds from '../da.service';
import { NotificationsService } from 'angular2-notifications';

@Component({
  selector: 'app-order-detail',
  templateUrl: './order-detail.component.html',
  styleUrls: ['./order-detail.component.css']
})
export class OrderDetailComponent implements OnInit {
  orderId;
  order;
  requestToPauseByClient = false;
  constructor(private route: ActivatedRoute,
    private _notifications: NotificationsService,
    private zone: NgZone,
    private router: Router,
    private orderServices: OrderServices) { }

  ngOnInit() {
    this.orderId = this.route.snapshot.params['id']
    this.fetchOrder(this.orderId);

    // ds.event.subscribe(`/providers/${environment.id}/orders/${this.orderId}`,
    ds.record.getRecord(`dsOrder/${this.orderId}`).subscribe(
      (res) => {
      
        if (res.requestToPauseByClient == true) {
          let confirm = window.confirm('are you want to PAUSED_BY_CLIENT');
          if (confirm == true) {
            this.pauseResume('PAUSED_BY_CLIENT');
          }
        }

        if (res.requestToResumeByClient == true) {
          let confirm = window.confirm('are you want to RESUEME_BY_CLIENT');
          if (confirm == true) {
            this.pauseResume('IN_PROGRESS');
          }
        }

        this.resetUnseenCount();

        if (res.status) {
          this.zone.run(() => {
            this.fetchOrder(this.orderId);
          });
        }
        console.log(this.orderId, 'the notifcation ', res, res.requestToPause);
      });

  }

  pauseResume(status) {
    this.orderServices.updateproviderorders(this.orderId, status).subscribe(
      res => {
        this.zone.run(() => {
          console.log('11111111111111111111111111111111111111');
          this.fetchOrder(this.orderId);
        });
      },
      err => {
        console.log('the error is ====>', err);
      })
  }

  fetchOrder(orderId) {
    this.orderServices.getOrderById(orderId).subscribe(res => {
      this.order = res;
      console.log('the order ==', res)
    })
  }

  resetUnseenCount() {
    this.orderServices.resetUnseenCount()
      .subscribe(result => {
        
      }
      )
  }



  open(text) {
    const toast = this._notifications.success(text, 'Click to undo...', {
      timeOut: 3000,
      showProgressBar: true,
      pauseOnHover: true,
      clickToClose: true,
      maxLength: 10,
      clickIconToClose: true
    });
  }

  changeStatus(id, status) {
    console.log(id, 'the status ====>', status);
    this.orderServices.updateproviderorders(id, status).subscribe(
      res => {
        // if(res.)
        this.zone.run(() => {
          this.fetchOrder(this.orderId);
        });
      },
      err => {
        this.open(err.error.error.message)
        console.log('the error is ====>', err.error.error.message);
      })

  }


  goToChat(client) {
    this.router.navigate(['orders', this.orderId, 'client', client.id, 'chat']);

  }
}
